@import url("https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&family=Yeon+Sung&display=swap");

body {
  background-color: #f6f6f6;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Nanum Pen Script";
  min-height: 100vh;
  margin: 0px;
}

@media (min-width: 650px) {
  .App {
    width: 640px;
  }
}

@media (max-width: 650px) {
  .App {
    width: 90vw;
  }
}

#root {
  background-color: white;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.App {
  min-height: 100vh;
  padding-left: 20px;
  padding-right: 20px;
}

.login_btn {
  margin: 20px;
  font-size: 15px;
  border-radius: 5px;
  border-color: #ececec;
}
/*  MyButton */
.MyButton {
  cursor: pointer;
  border: none;
  border-radius: 5px;

  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 20px;
  padding-right: 20px;

  font-size: 18px;

  white-space: nowrap;
  font-family: "Nanum Pen Script";
}

.MyButton_default {
  background-color: #ececec;
  color: black;
}

.MyButton_positive {
  background-color: #64c964;
  color: white;
}
.comment-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.MyButton_negative {
  background-color: #fd565f;
  color: white;
}
.delete-comment-button .MyButton_negative {
  display: flex;
}
.MyButton_negative:hover {
  background-color: #9a1015;
  color: white;
}
/* 홈 페이지 스타일 */

.Home {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.Home h1 {
  margin-bottom: 20px;
}

.Home h2 {
  margin-bottom: 10px;
}

.Home .topic_list {
  list-style-type: none;
  padding: 0;
}

.Home .topic_list li {
  margin-bottom: 20px;
}

.topic_list ul {
  margin: 0;
  padding: 0;
}
.topic_item {
  margin-bottom: 10px;
  list-style: none;
  align-items: center;
}

.topic_button {
  padding: 10px;
  border-radius: 5px;
  background-color: #ececec;
  color: black;
  font-size: 16px;
  font-family: "Nanum Pen Script";
}
.pagination {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.pagination ul {
  list-style: none;
  padding: 0;
  display: flex;
}

.pagination li {
  margin: 0 5px;
}

.pagination li span {
  display: inline-block;
  padding: 5px 10px;
  cursor: pointer;
  background-color: #ececec;
  color: black;
}

.pagination li span.active {
  background-color: #64c964;
  color: white;
}
/* 주제 생성 페이지 스타일 */

.CreateTopicPage {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
}

.CreateTopicPage h1 {
  font-size: 30px;
  margin-bottom: 20px;
}

.CreateTopicPage form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 400px;
}

.CreateTopicPage label {
  font-weight: bold;
  margin-bottom: 5px;
  width: 100px;
}

.CreateTopicPage input,
.CreateTopicPage textarea {
  padding: 10px;
  margin-bottom: 15px;
  border-radius: 4px;
  border: 1px solid #ccc;
  font-size: 16px;
  width: 100%;
}

.CreateTopicPage textarea {
  height: 200px;
  width: 450px;
  font-family: "Nanum Pen Script";
}

.CreateTopicPage button {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
  margin-right: 10px;
  padding: 10px 20px;
  font-size: 18px;
  border: none;
  border-radius: 4px;
  background-color: #212421;
  color: #fff;
  cursor: pointer;
}

.CreateTopicPage button:hover {
  background-color: #929496;
}

.CreateTopicPage .login-message button:hover {
  background-color: #929496;
}
/* 주제 페이지 스타일 */

.comment-input {
  width: 100%;
  height: 100px;
  resize: vertical;
  font-family: "Nanum Pen Script";
  font-size: 20px;
}

.comment-container {
  margin-top: 20px;
}

.comment-list {
  list-style: none;
  padding: 0;
}

.comment-item {
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  align-items: center;
  display: flex;
}

.comment-author {
  font-weight: bold;
  margin-right: 5px;
}

.comment-content {
  margin-top: 5px;
}

.TopicPage {
  margin: 20px;
}

.topic-container {
  background-color: #f6f6f6;
  padding: 20px;
  margin-bottom: 20px;
}

.TopicPage h2.content {
  padding: 10px;
}

.TopicPage form {
  margin-bottom: 20px;
}

.TopicPage label {
  display: block;
  margin-bottom: 10px;
}
.content {
  white-space: pre-line;
  word-wrap: break-word;
}
.TopicPage input[type="text"] {
  width: 100%;
  padding: 10px;
}

.TopicPage button[type="submit"] {
  padding: 10px 20px;
  background-color: #333;
  color: #fff;
  border: none;
  cursor: pointer;
}

.TopicPage ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.TopicPage ul li {
  margin-bottom: 10px;
}

.pagination {
  display: flex;
  justify-content: center;
}

.pagination ul {
  display: flex;
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.pagination ul li {
  margin-right: 5px;
}

.pagination ul li span {
  padding: 5px 10px;
  background-color: #eaeaea;
  cursor: pointer;
}

.pagination ul li span.active {
  background-color: #333;
  color: #fff;
}

.goback {
  text-align: right;
}

/* Login*/

.Login {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f5f5f5;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.Login h2 {
  margin-bottom: 20px;
  text-align: center;
}

.Login form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Login label {
  margin-bottom: 10px;
}

.Login input {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.Login button {
  padding: 8px 12px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  width: 100px;
}

.Login button:hover {
  background-color: #0056b3;
}

.Login button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}
/* HEADER */

header {
  padding-top: 20px;
  padding-bottom: 20px;

  display: flex;
  align-items: center;
  border-bottom: 1px solid #e2e2e2;
}

header > div {
  display: flex;
}
header {
  height: 40px;
}
header .head_text {
  width: 50%;
  font-size: 25px;
  justify-content: center;
}

header .head_btn_left {
  width: 25%;
  justify-content: start;
}

header .head_btn_right {
  width: 25%;
  justify-content: end;
}

header button {
  font-family: "Nanum Pen Script";
}
